export default {
	data() {
		return {
			title: '',
			strings: ''
		}
	},
	onLoad(e){
		console.log(e);
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsId,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.title=res.data.title;
				this.strings=res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
